import type { IProject } from '@interfaces/project'
import request from '@utils/request'


export const getProjects = (params = {}) => {
  return request({
    url: '/projects',
    method: 'get',
    params,
  })
}

export const createProject = (data: IProject) => {
  return request({
    url: '/projects',
    method: 'post',
    data,
  })
}

export const getProjectDetail = (id: string, params = {}) => {
  return request({
    url: `/projects/${id}`,
    method: 'get',
    params,
  })
}

export const updateProject = (id: string, data: IProject) => {
  return request({
    url: `/projects/${id}`,
    method: 'put',
    data,
  })
}

export const deleteProject = (id: string) => {
  return request({
    url: `/projects/${id}`,
    method: 'delete',
  })
}
