import { getUsers } from '@apis/users'
import { STATE, STATE_MAP } from '@constants/state'
import { Dropdown, Menu, Tag } from 'antd'
import Avatar from 'antd/lib/avatar/avatar'
import { Component } from 'react'
import { find } from 'lodash'
import { UserOutlined } from '@ant-design/icons'

type Props = {
  userId?: string
  callBack?: ($event) => void
}

type State = {
  current: any
  users: any
}

export default class ChargerComponent extends Component<Props, State> {
  constructor(props) {
    super(props)
    this.state = {
      current: null,
      users: [],
    }
  }
  async componentDidMount() {
    this.getUserList()
  }

  async getUserList() {
    const { data } = await getUsers()
    const { userId } = this.props
    this.setState({
      users: data,
      current: find(data, { _id: userId }),
    })
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.userId !== this.props.userId) {
      this.getUserList()
    }
  }

  userActions({ key }) {
    const { callBack } = this.props
    callBack && callBack(key)
  }
  render() {
    const { current, users } = this.state
    const stateMenu = (
      <Menu
        style={{ width: 200 }}
        onClick={(val) => {
          this.userActions(val)
        }}
      >
        {users.map((n) => {
          return <Menu.Item key={n._id}>{n.username}</Menu.Item>
        })}
      </Menu>
    )
    return (
      <Dropdown overlay={stateMenu}>
        <div>
          <Avatar style={{ backgroundColor: '#2db7f5' }} size={40}>
            {current ? current.username : <UserOutlined />}
          </Avatar>
        </div>
      </Dropdown>
    )
  }
}
