import type { Task } from '@interfaces/task'
import request from '@utils/request'

export const createTask = (data: Task) => {
  return request({
    url: '/tasks',
    method: 'post',
    data,
  })
}

export const updateTask = (_id: string, data: Task) => {
  return request({
    url: `/tasks/${_id}`,
    method: 'put',
    data,
  })
}

export const deleteTask = (_id: string) => {
  return request({
    url: `/tasks/${_id}`,
    method: 'delete',
  })
}

export const getTaskDetail = (_id: string, params = {}) => {
  return request({
    url: `/tasks/${_id}`,
    method: 'get',
    params,
  })
}

export const getTasks = (params = {}) => {
  return request({
    url: '/tasks',
    method: 'get',
    params,
  })
}

export const sortTasks = (
  _id: string,
  data: {
    fromId: string
    toId: string
    oldIndex: number
    newIndex: number
  },
) => {
  return request({
    url: `/tasks/${_id}/sort`,
    method: 'post',
    data,
  })
}

export const summaryTask = (data: { content: string; dateNumber: string }) => {
  return request({
    url: '/task-summary',
    method: 'post',
    data,
  })
}
export const getSummary = (params = {}) => {
  return request({
    url: '/task-summary',
    method: 'get',
    params,
  })
}
