import {
  DeleteOutlined,
  DownOutlined,
  EllipsisOutlined,
  LeftOutlined,
  PlusOutlined,
} from '@ant-design/icons'
import { getProjectDetail } from '@apis/project'
import { getProjectScrums, createScrum } from '@apis/scrum'
import { Drawer, Dropdown, Menu } from 'antd'
import Modal from 'antd/lib/modal/Modal'
import React, { Component } from 'react'
import styles from './index.module.less'
import i18n from 'i18next'
import { MODAL_SIZE } from '@constants/modal'
import ScrumCollecrtionComponent from './ScrumCollection'
import { find, groupBy } from 'lodash'
import TextArea from 'antd/lib/input/TextArea'
import { createTask, getTasks, getTaskDetail, deleteTask, updateTask, sortTasks } from '@apis/tasks'
import { ReactSortable } from 'react-sortablejs'
import TaskDetailComponent from '@components/TaskDetail'
import { STATE_MAP } from '@constants/state'
import Avatar from 'antd/lib/avatar/avatar'

export default class ProjectDetailView extends Component<any, any> {
  private detailModel = null
  constructor(props) {
    super(props)
    this.state = {
      current: null,
      scrums: [],
      project: null,
      isScrumCollectionVisible: false,
      groupTaskMap: {},
      isTaskDetailVisible: false,
      taskDetail: null,
      isDescriptionEditor: false,
    }
  }
  componentDidMount() {
    const { match } = this.props
    const _id = match.params.id
    this.getProject(_id)
    this.getScrums(_id)
  }
  async getProject(_id) {
    const project = await getProjectDetail(_id)
    this.setState({
      project,
    })
  }
  async getScrums(projectId) {
    const scrums = await getProjectScrums({
      projectId,
    })
    if (scrums) {
      this.setState({
        scrums,
        current: scrums[0],
      })
      this.getScrumTask(scrums[0]._id)
    }
  }

  async getScrumTask(scrum) {
    const tasks = await getTasks({
      scrum,
    })
    if (tasks) {
      this.setState({
        groupTaskMap: groupBy(tasks, 'group'),
      })
    }
  }

  selectMenu({ key }) {
    if (key === 'create') {
      this.setState({
        isScrumCollectionVisible: true,
      })
    } else {
      this.setState({
        current: find(this.state.scrums, { _id: key }),
      })
    }
  }

  closeCollection = () => {
    this.setState({
      isScrumCollectionVisible: false,
    })
  }

  async collectionCallBack({ isVisible, isRefresh, values = '' }) {
    if (values) {
      const scrum = await createScrum({
        // @ts-ignore
        ...values,
        project: this.state.project._id,
      })
      this.getScrums(this.state.project._id)
    }
    this.setState({
      isScrumCollectionVisible: false,
    })
  }

  async addTask(e, group) {
    if (e.keyCode === 13 && e.target.value) {
      const task = await createTask({
        name: e.target.value,
        group: group._id,
        scrum: this.state.current._id,
        project: this.state.project._id,
      })
      this.getScrumTask(this.state.current._id)

      setTimeout(() => {
        e.target.value = ''
      }, 200)
    }
  }

  async showTaskDetail(task) {
    const detail = await getTaskDetail(task._id)
    this.setState({
      taskDetail: detail,
    })
    if (this.detailModel) {
      this.detailModel.setVisible(true)
    }
  }

  setList(group, list) {
    this.state.groupTaskMap[group._id] = [...list]
    this.setState({
      groupTaskMap: { ...this.state.groupTaskMap },
    })
  }

  async updateChangeTask(key, { item, to, from, oldIndex, newIndex }) {
    const itemId = item.getAttribute('data-id')
    const fromId = from.getAttribute('id')
    const toId = to.getAttribute('id')
    await sortTasks(itemId, {
      fromId,
      toId,
      oldIndex,
      newIndex,
    })
  }

  getDetail(n) {
    n && (this.detailModel = n)
  }

  detailCallBack($event) {
    this.getScrumTask(this.state.current._id)
  }

  render() {
    const {
      project,
      scrums,
      isScrumCollectionVisible,
      current,
      groupTaskMap,
      isTaskDetailVisible,
      taskDetail,
      isDescriptionEditor,
    } = this.state

    const menu = (
      <Menu
        onClick={(val) => {
          this.selectMenu(val)
        }}
      >
        {scrums.map((n) => {
          return <Menu.Item key={n._id}>{n.name}</Menu.Item>
        })}
        <Menu.Divider />
        <Menu.Item key="create">
          <PlusOutlined /> 新建迭代
        </Menu.Item>
      </Menu>
    )

    return (
      <>
        <div className={styles['project-detail-wrap']}>
          <div className={styles['project-detail-header']}>
            <span className={styles['project-detail-title']}>
              <i className="cloud cloud-project m-r-4 m-l-12"></i>
              {project?.name}
            </span>
            <Dropdown overlay={menu}>
              <a className="ant-dropdown-link m-l-12" onClick={(e) => e.preventDefault()}>
                {current ? current.name : '迭代'} <DownOutlined />
              </a>
            </Dropdown>
          </div>
          <div className={styles['project-detail-body']}>
            {current && current.groups ? (
              <>
                {current.groups.map((n) => {
                  return (
                    <div
                      key={n._id}
                      className={`${styles['project-detail-group']} ${
                        n.type === 5 ? styles['project-detail-group-summary'] : ''
                      }`}
                    >
                      <div className={styles['project-detail-group-header']}>
                        <div>{n.name}</div>
                        {n.type !== 5 ? (
                          <div>
                            <TextArea
                              onKeyDown={(e) => {
                                this.addTask(e, n)
                              }}
                              rows={2}
                            />
                          </div>
                        ) : null}
                      </div>
                      <div className={styles['project-detail-group-body']}>
                        {groupTaskMap[n._id] ? (
                          <>
                            <ReactSortable
                              id={n._id}
                              group="tasks"
                              list={groupTaskMap[n._id]}
                              setList={(tasks) => this.setList(n, tasks)}
                              onEnd={(tasks) => this.updateChangeTask(n, tasks)}
                            >
                              {groupTaskMap[n._id].map((task) => {
                                return (
                                  <div
                                    key={task._id}
                                    className={`${styles['project-detail-group-body-task']} ${
                                      styles[STATE_MAP[task.state || 0].className]
                                    }`}
                                    onClick={() => {
                                      this.showTaskDetail(task)
                                    }}
                                  >
                                    <div className={styles.title}>{task.name}</div>
                                    <div>
                                      {task.charger ? (
                                        <Avatar style={{ backgroundColor: '#2db7f5' }} size={38}>
                                          {task.charger.username}
                                        </Avatar>
                                      ) : null}
                                    </div>
                                  </div>
                                )
                              })}
                            </ReactSortable>
                          </>
                        ) : null}
                      </div>
                    </div>
                  )
                })}
              </>
            ) : null}
          </div>
        </div>
        <Modal
          title={i18n.t('action.create')}
          visible={isScrumCollectionVisible}
          width={MODAL_SIZE.md}
          destroyOnClose={true}
          footer={false}
          onCancel={this.closeCollection.bind(this)}
        >
          {
            // @ts-ignore
            <ScrumCollecrtionComponent
              entity={null}
              callback={this.collectionCallBack.bind(this)}
            />
          }
        </Modal>
        <>
          {taskDetail ? (
            <TaskDetailComponent
              ref={(n) => {
                this.getDetail(n)
              }}
              taskId={taskDetail._id}
              afterAction={($event) => {
                this.detailCallBack($event)
              }}
            />
          ) : null}
        </>
      </>
    )
  }
}
