import { DeleteOutlined, EllipsisOutlined } from '@ant-design/icons'
import { deleteTask, getTaskDetail, updateTask } from '@apis/tasks'
import ChargerComponent from '@components/Charger'
import StateComponent from '@components/State'
import { STATE, STATE_MAP } from '@constants/state'
import { Drawer, Dropdown, Menu, Tag } from 'antd'
import TextArea from 'antd/lib/input/TextArea'
import React, { Component } from 'react'
import styles from './index.module.less'

type Props = {
  taskId: string
  afterAction?: ($event) => void
}

type State = {
  taskDetail: any
  isTaskDetailVisible: boolean
  isDescriptionEditor: boolean
}

export default class TaskDetailComponent extends Component<Props, State> {
  constructor(props) {
    super(props)
    this.state = {
      taskDetail: null,
      isTaskDetailVisible: false,
      isDescriptionEditor: false,
    }
  }

  componentDidMount() {
    const { taskId } = this.props
    this.getTask(taskId)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.taskId !== this.props.taskId) {
      this.getTask(nextProps.taskId)
    }
  }

  async getTask(taskId) {
    const detail = await getTaskDetail(taskId)
    if (detail) {
      this.setState({
        taskDetail: detail,
      })
    }
  }

  setVisible(isTaskDetailVisible) {
    this.setState({
      isTaskDetailVisible,
    })
  }
  showDescriptionEditor() {
    this.setState({
      isDescriptionEditor: true,
    })
  }

  async saveDescription(e) {
    const task = await updateTask(this.state.taskDetail._id, {
      description: e.target.value,
    })
    this.setState({
      taskDetail: task,
      isDescriptionEditor: false,
    })
  }

  onCloseDetail = () => {
    this.setState({
      isTaskDetailVisible: false,
    })
  }

  async taskActions({ key }) {
    if (key === 'delete') {
      await deleteTask(this.state.taskDetail._id)
      this.onCloseDetail()
    }
    const { afterAction } = this.props
    afterAction && afterAction(null)
  }

  async taskStateActions({ key }) {
    const dd = await updateTask(this.state.taskDetail._id, {
      state: key,
    })
  }

  async changeCharger(charger) {
    const taskDetail = await updateTask(this.state.taskDetail._id, {
      charger,
    })
    if (taskDetail) {
      this.setState({
        taskDetail,
      })
    }
  }

  async changeState(state) {
    const taskDetail = await updateTask(this.state.taskDetail._id, {
      state,
    })
    if (taskDetail) {
      this.setState({
        taskDetail,
      })
    }
  }

  render() {
    const { taskDetail, isTaskDetailVisible, isDescriptionEditor } = this.state
    const detailMenu = (
      <Menu
        onClick={(val) => {
          this.taskActions(val)
        }}
      >
        <Menu.Item key="delete">
          <DeleteOutlined />
          删除
        </Menu.Item>
      </Menu>
    )

    return (
      <Drawer
        title={null}
        placement="right"
        width={1024}
        destroyOnClose={true}
        closable={false}
        onClose={this.onCloseDetail}
        visible={isTaskDetailVisible}
      >
        <div className={styles['detail-wrap']}>
          <div className={styles['detail-header']}>
            <div className={styles.title}>{taskDetail && taskDetail.name}</div>
            <div className={styles.actions}>
              <Dropdown overlay={detailMenu}>
                <EllipsisOutlined />
              </Dropdown>
            </div>
          </div>
          <div className={styles['detail-body']}>
            <div className={styles['detail-tools']}>
              <div className="m-r-12">
                <ChargerComponent
                  userId={taskDetail?.charger}
                  callBack={($event) => {
                    this.changeCharger($event)
                  }}
                />
              </div>
              <StateComponent
                status={taskDetail?.state}
                callBack={($event) => {
                  this.changeState($event)
                }}
              />
            </div>
            {isDescriptionEditor ? (
              <div className={styles['description-editor']}>
                <TextArea
                  defaultValue={taskDetail && taskDetail.description ? taskDetail.description : ''}
                  onBlur={(e) => {
                    this.saveDescription(e)
                  }}
                  rows={10}
                />
              </div>
            ) : (
              <div
                className={styles.description}
                onClick={() => {
                  this.showDescriptionEditor()
                }}
              >
                {taskDetail && taskDetail.description ? taskDetail.description : '添加描述'}
              </div>
            )}
          </div>
        </div>
      </Drawer>
    )
  }
}
