import type { IScrum } from '@interfaces/scrum'
import request from '@utils/request'

export const getProjectScrums = (params = {}) => {
    return request({
      url: '/scrums',
      method: 'get',
      params,
    })
  }

  export const createScrum = (data: IScrum) => {
    return request({
      url: '/scrums',
      method: 'post',
      data,
    })
  }