import { STATE, STATE_MAP } from '@constants/state'
import { Dropdown, Menu, Tag } from 'antd'
import { Component } from 'react'

type Props = {
  status?: number
  callBack?: ($event) => void
}

type State = {
  current: any
}

export default class StateComponent extends Component<Props, State> {
  constructor(props) {
    super(props)
    this.state = {
      current: null,
    }
  }
  componentDidMount() {
    const { status } = this.props
    this.setState({
      current: STATE_MAP[status || 'PENDING'],
    })
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.status !== this.props.status) {
      this.setState({
        current: STATE_MAP[nextProps.status || 'PENDING'],
      })
    }
  }

  stateActions({ key }) {
    const { callBack } = this.props
    callBack && callBack(key)
  }
  render() {
    const { current } = this.state
    const stateMenu = (
      <Menu
        style={{ width: 200 }}
        onClick={(val) => {
          this.stateActions(val)
        }}
      >
        <Menu.Item key={STATE.PENDING}>未开始</Menu.Item>
        <Menu.Item key={STATE.DOING}>进行中</Menu.Item>
        <Menu.Item key={STATE.DONE}>已完成</Menu.Item>
      </Menu>
    )
    return (
      <Dropdown overlay={stateMenu}>
        <div>
          <Tag color={current?.color}>{current?.label}</Tag>
        </div>
      </Dropdown>
    )
  }
}
