import React, { Component } from 'react'
import { Form, Input, Button } from 'antd'
import i18n from 'i18next'
import { isPhone } from '@utils/index'
import type { IProject } from '@interfaces/project'
import TextArea from 'antd/lib/input/TextArea'

type IScrumCollectionProps = {
  callback: ({ isVisible, isRefresh, values }) => unknown
  form: any
  entity: IProject
}
type IScrumCollectionState = {
  loading: boolean
}

export default class ScrumCollecrtionComponent extends Component<
  IScrumCollectionProps,
  IScrumCollectionState
> {
  constructor(props) {
    super(props)
    this.state = {
      loading: false,
    }
  }
  componentDidMount() {
    // console.log(this.props.entity);
  }
  cancel(e) {
    e.preventDefault()
    this.props.callback({
      isVisible: false,
      isRefresh: false,
      values: '',
    })
  }
  handleSubmit(values) {
    this.props.callback({
      isVisible: false,
      isRefresh: true,
      values,
    })
  }

  render() {
    const { entity } = this.props
    return (
      <Form
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        onFinish={this.handleSubmit.bind(this)}
        initialValues={{
          name: entity ? entity.name : '',
          description: entity ? entity.description : '',
        }}
      >
        <Form.Item label="名称" name="name" rules={[{ required: true, message: '请输入迭代名' }]}>
          <Input placeholder="请输入迭代名" />
        </Form.Item>
        <Form.Item
          label="描述"
          name="description"
          rules={[{ required: true, message: '请输入描述' }]}
        >
          <TextArea rows={4} placeholder="请输入描述" />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="m-r-12">
            {i18n.t('action.save')}
          </Button>
          <Button onClick={this.cancel.bind(this)}> {i18n.t('action.cancel')}</Button>
        </Form.Item>
      </Form>
    )
  }
}
